:root {
  color-scheme: light dark;
}

body {
    background-color: light-dark(white, #191919);
}

main {
    max-width: 800px;
    margin: auto;
}

h1 {
    font-family: "Battambang", Sans-serif;
    font-weight: 700;
    line-height: 1.33em;
    letter-spacing: -0.15px;
    color: light-dark(#2436B4, #f7e8b1);
}

p, details, li {
    font-family: "League Spartan", Sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 1.4em;
    color: light-dark(#676262, #c1baba);
}

.sources > summary {
    border: 2px solid light-dark(#676262, #c1baba);
    border-radius: 8px;
    width:fit-content;
    padding: 0.5em;
    cursor: pointer;
}

.statue {
    display: block;
    object-fit: cover;
     border-radius: 0px;
     width: 100%;
    height: 30vh;
    max-height: 280px;
    opacity: 1;
    object-position: center 50%;
}

.red {
    font-weight: 700;
    color: light-dark(rgb(118, 17, 17), rgb(217, 115, 115));
}

:target {
    background-color: light-dark(rgb(241, 208, 164), rgb(0, 49, 102));
}
